// 网站标题控制
let routeTitle = "";
let siteTitle = ""
let tit = document.querySelector("title");

function setTitle() {
    if (!routeTitle && !siteTitle) {
        tit.innerHTML = "加载中...";
    } else if (routeTitle && !siteTitle) {
        tit.innerHTML = routeTitle;
    } else if (!routeTitle && siteTitle) {
        tit.innerHTML = siteTitle;
    } else {
        tit.innerHTML = `${routeTitle}-${siteTitle}`;
    }
}
export default {
    // 设置路由标题
    setRouteTitle(title) {
        routeTitle = title;
        setTitle();
    },
    // 设置文章标题
    setSiteTitle(title) {
        siteTitle = title;
        setTitle();
    }
}