import Vue from "vue";

/**
 * 获取某个组件渲染的Dom根元素
 * @param {Object} comp传入组件对象
 * @param {Object} props 组件的属性 注:这里要传入的是对象属性
 * @returns 这个组件生成的DOM元素
 */
export default function(comp, props) {
    const vm = new Vue({
        // 渲染组件
        render: (h) => h(comp, { props }),
    });
    // 挂载
    vm.$mount();
    // 返回生成的节点
    return vm.$el;
}