import Mock from "mockjs";
import qs from "querystring"; //第三方库获取url地址数据
Mock.mock("/api/message", "post", {
    code: 0,
    msg: "",
    data: {
        id: "@guid",
        nickname: "@cname",
        content: "@cparagraph(1,10)",
        createDate: Date.now(),
        "avatar|1": [
            "https://img2.baidu.com/it/u=4244269751,4000533845&fm=253&fmt=auto&app=138&f=JPEG?w=500&h=500",
            "https://img1.baidu.com/it/u=3363858394,2140002617&fm=253&fmt=auto&app=138&f=JPEG?w=500&h=500",
            "https://img1.baidu.com/it/u=4126345688,2740482511&fm=253&fmt=auto&app=138&f=JPEG?w=400&h=400",
            "https://img1.baidu.com/it/u=289475012,3923945794&fm=253&fmt=auto&app=138&f=JPEG?w=500&h=500",
        ],
    }
})

Mock.mock(/^\/api\/message\/?(\?.+)?$/, "get", function(options) {
    const query = qs.parse(options.url);
    return Mock.mock({
        code: 0,
        msg: "",
        data: {
            total: 52,
            [`rows|${query.limit|| 10}`]: [{
                id: "@guid",
                nickname: "@cname",
                content: "@cparagraph(1,10)",
                createDate: Date.now(),
                "avatar|1": [
                    "https://img2.baidu.com/it/u=4244269751,4000533845&fm=253&fmt=auto&app=138&f=JPEG?w=500&h=500",
                    "https://img1.baidu.com/it/u=3363858394,2140002617&fm=253&fmt=auto&app=138&f=JPEG?w=500&h=500",
                    "https://img1.baidu.com/it/u=4126345688,2740482511&fm=253&fmt=auto&app=138&f=JPEG?w=400&h=400",
                    "https://img1.baidu.com/it/u=289475012,3923945794&fm=253&fmt=auto&app=138&f=JPEG?w=500&h=500",
                ],
            }]
        }
    })

})