// 为了让主文件更加简介把路由的配置文件单独拿出来放在这个文件夹下面
import Vue from "vue";
import VueRouter from "vue-router";
import routes from "./routes";
import { titleController } from "@/utils/index"; //导入写好的设置标题的工具函数
// 传统引入就是不使用依赖使用script引入的话会污染全局变量
// 而且不能使用安装插件的代码，他会自动安装的所以要判断一下
// 判断不存在的话才使用这句话
if (!window.VueRouter) {
    Vue.use(VueRouter); // 使用一个vue插件
}
const router = new VueRouter({
    // 配置文件
    // 当匹配到路径path配置的路径时，渲染 component配置的组件
    // 因为我们给配置的文件取名routes所以我们可以简写为下面的方式
    routes, // 路由匹配规则
    mode: "history", //配置路由模式
});
// 跳转以后
router.afterEach((to, from) => {
    titleController.setRouteTitle(to.meta.title);
})
export default router; //把写好的匹配文件导出