import getComponentRootDom from "./getComponentRootDom"; //导入工具文件
import Icon from "@/components/Icon"; //导入图标组件
import styles from "./showMessage.module.less";

/**
 * 里面有很多参数为了优化代码写成为一个对象
 * @param {Object} parameter
 * @param {String} content 消息内容
 * @param {String} type 消息类型 info通知 error错误 success成功 warn警告
 * @param {Number} duration 多久消失
 * @param {HTML} container 容器DOM节点，可以不传，如果不传就在页面的中心显示
 * @param {Function} callback 回调函数
 */
export default function(options = {}) {
    //导出一个匿名函数
    const content = options.content || "";
    const type = options.type || "info";
    const duration = options.duration || 2000;
    const container = options.container || document.body;
    // 创建消息元素
    const div = document.createElement("div");
    // 得到图标组件所创建好的模板节点
    const iconDom = getComponentRootDom(Icon, {
        type,
    });
    div.innerHTML = `<span class="${styles.icon}">${iconDom.outerHTML}</span><div>${content}</div>`;
    // 设置样式
    const typeClassName = styles[`message-${type}`]; //类型样式名
    div.className = `${styles.message} ${typeClassName}`;
    // 将div加入到容器中

    // 容器的position是否改动过
    if (options.container) {
        if (getComputedStyle(container).position === "static") {
            container.style.position = "relative";
        }
    }
    // 把创建好的div节点放入容器节点中
    container.appendChild(div);
    // 浏览器强行渲染
    div.clientHeight; // 导致reflow

    // 回归到正常位置
    div.style.opacity = 1;
    div.style.transform = `translate(-50%, -50%)`;

    // 设置定时器多长时间以后创建的div消失
    setTimeout(() => {
        div.style.opacity = 0;
        // 位移消失
        div.style.transform = `translate(-50%, -50%) translateY(-25px)`;
        // 绑定transitionend事件
        //transitionend事件是过度结束事件，第三个参数{once:true}是表示让这个事件只执行一次
        div.addEventListener(
            "transitionend",
            function() {
                div.remove();
                // 想让它消失以后执行回调函数
                options.callback && options.callback();
            }, { once: true }
        );
    }, duration);
}