import { getProjects } from "@/api/project"; //导入请求api的方法
export default {
    namespaced: true, //开启命名空间
    state: {
        isLoading: false, //是否需要加载效果
        data: [], //原创请求的数据
    },
    mutations: {
        setLoading(state, payload) {
            state.isLoading = payload; //是否需要加载效果
        },
        setData(state, payload) {
            state.data = payload; //改变数据
        }
    },
    actions: {
        // 远程获取数据
        async fetchProject(cte) {
            if (cte.state.data.length) {
                return; //判断当前是否已经请求过值了请求过就不需要再次请求了
            }
            // 需要加载
            cte.commit("setLoading", true);
            // 远程获取数据
            const resp = await getProjects();
            // 把获取的数据放入仓库data中
            cte.commit("setData", resp);
            // 需要加载完成了
            cte.commit("setLoading", false);
        }
    }
}