import { getBanners } from "@/api/banner";
export default {
    namespaced: true, //开启命名空间
    state: {
        isLoading: false, //是否需要加载效果
        data: [], //轮播图数据
    },
    mutations: {
        setLoading(state, payload) {
            state.isLoading = payload; //是否需要加载效果
        },
        setData(state, payload) {
            state.data = payload;
        }
    },
    actions: {
        async fetchBanner(cxt) {
            if (cxt.state.data.length) { //判断当前是否已经请求过值了请求过就不需要再次请求了
                return;
            }
            cxt.commit("setLoading", true);
            const resp = await getBanners();
            cxt.commit("setData", resp);
            cxt.commit("setLoading", false);
        }
    }

}