// 创建vuex仓库
// vuex仓库可以使用树摇优化（树摇优化依赖具名导出,vuex可以具名导出）
import Vue from "vue";
import { Store, install } from "vuex";
import banner from "./banner";
import setting from "./setting";
import about from "./about";
import project from "./project";
// 传统引入就是不使用依赖使用script引入的话会污染全局变量
// 而且不能使用安装插件的代码，他会自动安装的所以要判断一下
// 判断不存在的话才使用这句话
if (!window.Vuex) {
    install(Vue); //安装vuex插件
}

export default new Store({
    modules: {
        banner, //轮播图
        setting, //全局设置
        about, //关于我
        project, //项目&demo
    },
    strict: true, //开启严格模式
})