// 导入404的组件
import NotFound from "@/views/NotFound.vue"

// 导入进度条第三方库的css
import "nprogress/nprogress.css";
import { start, done, configure } from "nprogress/nprogress.js";
// 对这个第三方库进行全局配置
configure({
    rtickleSpeed: 20, //配置进度条速度
    showSpinner: false, //关机右上角的小圈
})

// 配置一个异步等待函数
function delay(time) {
    // 返回一个promise
    return new Promise((resolve) => {
        // 等time秒后返回成功
        setTimeout(() => {
            resolve();
        }, time)
    });
}
// 配置一个路由懒加载
function getPageComponent(pageCompResolver) {
    // 因为返回的是一个promise所以肯定是一个异步的
    return async() => {
        // 组件开始加载的位置
        start();
        // 判断当前环境是否为开发环境(如果为开发环境的话就使用上面配置的异步等待函数模拟等待)
        if (process.env.NODE_ENV === "development") {
            await delay(2000);
        }
        // 判断完了以后执行下列代码
        const comp = await pageCompResolver();
        // 组件结束加载的位置
        done();
        return comp;
    }

}


// 配置文件
// 当匹配到路径(location.hash)path配置的路径时，渲染 component配置的组件
// component进行动态导入（因为动态导入的东西会单独分包，并且公共的东西会提取出来）
// 页面分包（为了让每一个页面单独进行打包）
// 这个东西其实叫路由懒加载
export default [{
        name: "Home",
        path: "/",
        component: getPageComponent(() =>
            import ( /* webpackChunkName: "Home" */ "@/views/Home")),
        meta: { title: "首页" }
    },
    {
        name: "About",
        path: "/about",
        component: getPageComponent(() =>
            import ( /* webpackChunkName: "About" */ "@/views/About")),
        meta: { title: "关于我" }
    },
    {
        name: "Blog",
        path: "/article",
        component: getPageComponent(() =>
            import ( /* webpackChunkName: "Blog" */ "@/views/Blog")),
        meta: { title: "文章" }
    },
    {
        name: "CategoryBlog",
        path: "/article/cate/:categoryId",
        component: getPageComponent(() =>
            import ( /* webpackChunkName: "Blog" */ "@/views/Blog")),
        meta: { title: "文章" }
    },
    {
        name: "BlogDetail",
        path: "/article/:id",
        component: getPageComponent(() =>
            import ( /* webpackChunkName: "BlogDetail" */ "@/views/Blog/Detail")),
        meta: { title: "文章详情" }
    },
    {
        name: "Project",
        path: "/project",
        component: getPageComponent(() =>
            import ( /* webpackChunkName: "Project" */ "@/views/Project")),
        meta: { title: "项目&效果" }
    },
    {
        name: "Message",
        path: "/message",
        component: getPageComponent(() =>
            import ( /* webpackChunkName: "Message" */ "@/views/Message")),
        meta: { title: "留言板" }
    },
    {
        name: "NotFound",
        path: "*", //通配符前面都没有匹配就匹配他
        component: NotFound,
        meta: { title: "404" }
    }
];