import { getSetting } from '@/api/setting';
import { titleController } from "@/utils/index"; //导入写好的设置标题的工具函数

export default { //配置仓库
    namespaced: true, //开启命名空间
    state: {
        isLoading: false, //是否需要加载效果
        data: null,
    }, //轮播图数据
    mutations: {
        setLoading(state, payload) {
            state.isLoading = payload; //是否需要加载效果
        },
        setData(state, payload) {
            state.data = payload; //把远程获取到的
        }
    },
    actions: {
        async fetchSetting(cxt) { //获取并且传入值的方法
            cxt.commit("setLoading", true);
            const resp = await getSetting();
            cxt.commit("setData", resp);
            cxt.commit("setLoading", false);
            if (resp.favicon) { //表示有值
                let link = document.querySelector("link[rel='shortcut icon']");
                if (link) {
                    return //如果有这个标签了就跳出
                }
                link = document.createElement("link");
                link.rel = "shortcut icon";
                link.type = "images/x-icon";
                link.href = resp.favicon;
                document.querySelector("head").appendChild(link);
            }
            // 其实这个可以直接写在上面那个判断中因为在上面的判断中就判断了resp中是否有值
            if (resp.siteTitle) {
                titleController.setSiteTitle(resp.siteTitle);
            }

        }
    }
}